import { Component, OnInit } from '@angular/core';
import { Observable, Subject, BehaviorSubject } from 'rxjs';
import { WundergroundService } from '../shared/wunderground.service';

@Component({
  moduleId: module.id,
  selector: 'cd-searchbar',
  template: require('./searchbar.component.html'),
  styles: [require('./autocomplete.css')],
  providers: [WundergroundService]
})
export class SearchbarComponent implements OnInit {
  suggestions: Observable<Array<string>>; /// make this an input
  searchTerm: string;
  dropdownStyle: any;
  searchTermSubject: Subject<string>;
  termSelected = false;

  constructor(private wundergroundService: WundergroundService) {
    this.suggestions  = Observable.of(['one', 'two', 'three', 'four', 'five']);
  }

  ngOnInit() {
    this.searchTerm = '';
    this.dropdownStyle = {display: 'none'};
    this.searchTermSubject = new BehaviorSubject<string>(null);
  }

  suggestionSelected(suggestion: string) {
    this.termSelected = true;
    console.log(`Suggestion selected: ${suggestion}`);
  }

  keyup(): void {
    console.log(`Search term: ${this.searchTerm}`);
    // toggle visability of display
    if (!this.searchTerm || this.searchTerm === '') {
      this.dropdownStyle = {display: 'none'};
    } else {
      this.dropdownStyle = {display: 'block'};
      this.searchTermSubject.next(this.searchTerm);
      this.wundergroundService.getCities(this.searchTermSubject)
        .subscribe( response => {
          console.log(`Response`, response);
        });
    }
  }

}



