import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs';

const DEBOUNCE_TIME = 750;
const AUTOCOMPLETE_URL = 'http://autocomplete.wunderground.com/aq?query=';

@Injectable()
export class WundergroundService {

  constructor(private http: Http) { }

  getCities(searchTerms: Observable<string>) {
    return searchTerms
      .debounceTime(DEBOUNCE_TIME)
      .distinctUntilChanged()
      .switchMap(term => this.autocompleteSearch(term));
  }

  autocompleteSearch(term: string) {
    return this.http.get(`${AUTOCOMPLETE_URL}${term}`)
      .map(request => request.json());
  }
}



